.statistic__cards {
  display: flex;
  justify-content: space-between;
  gap: 94px;
}
@media (max-width: 768px) {
  .statistic__cards {
    flex-direction: column;
  }
}

.statistic__card {
  display: flex;
  flex-direction: column;
  place-items: center;
  text-align: center;
  max-width: 399px;
  gap: 16px;
}
@media (max-width: 1025px) {
  .statistic__card {
    max-width: 190px;
  }
}
@media (max-width: 768px) {
  .statistic__card {
    max-width: inherit;
    width: 100%;
  }
}

.statistic__card-title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1px;
  text-wrap: balance;
}

.statistic__card-text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
}